import React from "react";
import {Box, Heading, theme} from "@mechatronick/datum-design-system"
import FlexWrapper from "../FlexWrapper";
import {ELEMENT_TYPE} from "../../constant"
import ElementOfPage from "../ElementOfPage"
import {graphql, useStaticQuery} from "gatsby";
import styled from "styled-components"


const inputData = [
    {
        type: ELEMENT_TYPE.TEXT_P,
        text: "Datum Consulting - an engineering and digital transformation " +
            "consultancy based in Auckland, New Zealand."
    },
    {
        type: ELEMENT_TYPE.TEXT_P,
        text: "Since 2017, we have worked in companies ranging from the " +
            "size of startup to one of the the 4 big banks of Australia, to " +
            "help our clients build quality software and undergo digital transformation."
    },
    {
        type: ELEMENT_TYPE.TEXT_P,
        text: "Launching a product? Not sure what your digital strategy is? " +
            "Need help with an existing projects?"
    },
    {
        type: ELEMENT_TYPE.BUTTON_GATSBY_LINK,
        text: `WORK WITH US`,
        to: `/contact/`
    }
];

const StyledFlexWrapper = styled(FlexWrapper)`
  text-align: center;
  align-items: center;
  flex-direction: column-reverse;
  ${theme.mediaQueries.md} {
    flex-direction: row;
  }
`;

const StyledBox = styled(Box)`
  box-shadow: ${theme.boxShadows[1]};
  ${theme.mediaQueries.md} {
    box-shadow: none;
  }
`;

export default () => {
    const {image} = useStaticQuery(
        graphql`
            query {
                image: file(relativePath: { eq: "startup.jpg" }) {
                    childImageSharp {
                        fluid(maxWidth: 900) {
                            ...GatsbyImageSharpFluid
                        }
                    }
                }
            }
        `
    );
    const imageScr = image.childImageSharp.fluid.src;
    return (
        <>
            <Heading.h2
                style={
                    {
                        textAlign: "center",
                        padding: `${theme.space[4]}px 0`
                    }
                }
            >Our story</Heading.h2>
            <StyledFlexWrapper
                wrap={"wrap"}
            >
                <Box
                    width={[1, 1, 2 / 3]}
                    p={[2, 2, 3]}
                >
                    <StyledBox
                        p={[3, 3, 3]}
                    >
                        {inputData.map((element, index) => {
                            return (
                                <ElementOfPage key={index} {...element} />
                            );
                        })}
                    </StyledBox>
                </Box>
                <Box
                    width={[1, 1, 1 / 3]}
                    px={[2, 2, 3]}
                    style={{objectFit: "scale-down"}}
                >
                    <img
                        alt={"Datum Consulting : Our Story"}
                        title={"Datum Consulting - an engineering and " +
                        "digital transformation " +
                        "consultancy based in Auckland, New Zealand."}
                        width={"100%"}
                        height={"auto"}
                        src={imageScr}
                    />
                </Box>
            </StyledFlexWrapper>
        </>
    )
}